import JSZip from "jszip";

// Master list from the PDF - ICREIT Investor Numbers
export const ICREIT_NUMBERS = [
  "IC001", "IC003", "IC004", "IC005", "IC006", "IC007", "IC008", "IC009",
  "IC012", "IC013", "IC014", "IC015", "IC016", "IC017", "IC018", "IC022",
  "IC024", "IC028", "IC029", "IC032", "IC033", "IC034", "IC036", "IC037",
  "IC038", "IC039", "IC049", "IC050", "IC051", "IC053", "IC055", "IC056",
  "IC058", "IC059", "IC060", "IC061", "IC062", "IC065", "IC086", "IC118",
  "IC119", "IC120", "IC121", "IC133", "IR134"
];

// Master list from the PDF - ICIF Investor Numbers
export const ICIF_NUMBERS = [
  "IC010", "IC011", "IC019", "IC020", "IC023", "IC025", "IC026", "IC027",
  "IC030", "IC031", "IC035", "IC040", "IC041", "IC042", "IC044", "IC045",
  "IC046", "IC047", "IC048", "IC052", "IC054", "IC057", "IC064", "IC066",
  "IC067", "IC068", "IC069", "IC071", "IC072", "IC073", "IC074", "IC075",
  "IC077", "IC078", "IC079", "IC080", "IC081", "IC082", "IC083", "IC084",
  "IC085", "IC087", "IC088", "IC089", "IC090", "IC091", "IC092", "IC093",
  "IC094", "IC095", "IC096", "IC097", "IC098", "IC099", "IC100", "IC101",
  "IC102", "IC103", "IC104", "IC105", "IC106", "IC107", "IC108", "IC109",
  "IC110", "IC111", "IC112", "IC113", "IC114", "IC115", "IC122", "IC123",
  "IC124", "IC125", "IC126", "IC127", "IC128", "IC129", "IC130", "IC131",
  "IC132", "IC134", "IC135", "IC136", "IC137", "IC138", "IC139", "IC140",
  "IC141", "IC142"
];

export interface SortResult {
  ICREIT: JSZip;
  ICIF: JSZip;
  reitFiles: string[];
  icifFiles: string[];
  unmatched: string[];
  stats: {
    totalFiles: number;
    icreitCount: number;
    icifCount: number;
    unmatchedCount: number;
  };
}

export async function processZip(file: File): Promise<SortResult> {
  try {
    // Create new zip instances and file arrays for each category
    const reitZip = new JSZip();
    const icifZip = new JSZip();
    const reitFiles: string[] = [];
    const icifFiles: string[] = [];
    const unmatched: string[] = [];
    
    // Regular Expression for matching "IC" numbers (e.g., IC001, IC123, IR134)
    const icRegex = /(I[CR]\d{3})/g;
    
    let totalFiles = 0;
    let icreitCount = 0;
    let icifCount = 0;

    // Check if the file is a ZIP file
    if (file.name.toLowerCase().endsWith('.zip')) {
      // Process ZIP file
      const loadedZip = await JSZip.loadAsync(file);
      
      // Process each file in the zip
      const filePromises = Object.keys(loadedZip.files).map(async (filename) => {
        const fileEntry = loadedZip.files[filename];
        
        // Skip directories
        if (fileEntry.dir) return;
        
        totalFiles++;
        
        // Find all IC numbers in the filename
        const matches = filename.match(icRegex);
        let categorized = false;
        
        if (matches) {
          for (const match of matches) {
            const icNumber = match.toUpperCase();
            
            if (ICREIT_NUMBERS.includes(icNumber)) {
              // Add file to ICREIT zip and track filename
              const content = await fileEntry.async("uint8array");
              reitZip.file(filename, content);
              reitFiles.push(filename);
              icreitCount++;
              categorized = true;
              break; // Stop after first match to avoid duplicates
            } else if (ICIF_NUMBERS.includes(icNumber)) {
              // Add file to ICIF zip and track filename
              const content = await fileEntry.async("uint8array");
              icifZip.file(filename, content);
              icifFiles.push(filename);
              icifCount++;
              categorized = true;
              break; // Stop after first match to avoid duplicates
            }
          }
        }
        
        // If no valid mapping is found, mark as unmatched
        if (!categorized) {
          unmatched.push(filename);
        }
      });
      
      await Promise.all(filePromises);
    } else {
      // Process individual file
      totalFiles = 1;
      const filename = file.name;
      
      // Find all IC numbers in the filename
      const matches = filename.match(icRegex);
      let categorized = false;
      
      if (matches) {
        for (const match of matches) {
          const icNumber = match.toUpperCase();
          
          if (ICREIT_NUMBERS.includes(icNumber)) {
            // Add file to ICREIT zip and track filename
            const content = await file.arrayBuffer();
            reitZip.file(filename, content);
            reitFiles.push(filename);
            icreitCount++;
            categorized = true;
            break; // Stop after first match to avoid duplicates
          } else if (ICIF_NUMBERS.includes(icNumber)) {
            // Add file to ICIF zip and track filename
            const content = await file.arrayBuffer();
            icifZip.file(filename, content);
            icifFiles.push(filename);
            icifCount++;
            categorized = true;
            break; // Stop after first match to avoid duplicates
          }
        }
      }
      
      // If no valid mapping is found, mark as unmatched
      if (!categorized) {
        unmatched.push(filename);
      }
    }
    
    return {
      ICREIT: reitZip,
      ICIF: icifZip,
      reitFiles,
      icifFiles,
      unmatched,
      stats: {
        totalFiles,
        icreitCount,
        icifCount,
        unmatchedCount: unmatched.length
      }
    };
  } catch (error) {
    throw new Error("Invalid or corrupted file. Please ensure you're uploading a valid file.");
  }
}
