"use client";

import React, { useState, useCallback } from "react";
import { processZip, SortResult, ICREIT_NUMBERS, ICIF_NUMBERS } from "@/lib/sortZip";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

export default function Page() {
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<SortResult | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [isDragging, setIsDragging] = useState(false);

  // Handler for file selection / drop
  const handleFile = useCallback(async (file: File) => {
    // Reset previous state
    setError(null);
    setResult(null);
    setLoading(true);
    
    try {
      const sortResult = await processZip(file);
      setResult(sortResult);
    } catch (e: any) {
      setError(e.message || "An error occurred while processing the zip file.");
    } finally {
      setLoading(false);
    }
  }, []);

  // onChange handler for the file input
  const onFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      handleFile(file);
    } else {
      setError("Please select a file.");
    }
  };

  // Download a zip blob given a JSZip instance and a filename
  const handleDownload = async (zipInstance: any, filename: string) => {
    try {
      const blob = await zipInstance.generateAsync({ type: "blob" });
      const url = URL.createObjectURL(blob);
      const anchor = document.createElement("a");
      anchor.href = url;
      anchor.download = filename;
      document.body.appendChild(anchor);
      anchor.click();
      document.body.removeChild(anchor);
      URL.revokeObjectURL(url);
    } catch (e) {
      console.error("Download error", e);
      setError("Failed to generate download file.");
    }
  };

  // Handlers for drag events
  const onDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const onDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const onDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
    const file = e.dataTransfer.files[0];
    if (file) {
      handleFile(file);
    } else {
      setError("Please drop a file.");
    }
  };

  const resetState = () => {
    setResult(null);
    setError(null);
    setLoading(false);
  };

  return (
    <div className="min-h-screen bg-background py-8 px-4">
      <div className="max-w-4xl mx-auto space-y-8">
        {/* Header */}
        <div className="text-center space-y-2">
          <h1 className="text-3xl font-bold tracking-tight">Document Sorter</h1>
          <p className="text-muted-foreground">
            Upload files to sort documents by IC numbers into ICREIT and ICIF categories
          </p>
        </div>

        {/* Upload Section */}
        <Card>
          <CardHeader>
            <CardTitle>Upload File</CardTitle>
          </CardHeader>
          <CardContent>
            <div
              onDragOver={onDragOver}
              onDragLeave={onDragLeave}
              onDrop={onDrop}
              className={`border-2 border-dashed rounded-lg p-12 text-center cursor-pointer transition-all duration-200 ${
                isDragging 
                  ? "border-primary bg-primary/5 scale-105" 
                  : "border-muted-foreground/25 hover:border-primary/50 hover:bg-muted/50"
              }`}
              onClick={() => {
                document.getElementById("fileInput")?.click();
              }}
            >
              <div className="space-y-4">
                <div className="text-4xl">üìÅ</div>
                  <p className="text-lg font-medium mb-2">
                    {isDragging ? "Drop your file here" : "Drag and drop your file here"}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    ZIP files will be processed for sorting, individual files will be categorized
                  </p>
                </div>
              <input
                id="fileInput"
                type="file"
                className="hidden"
                onChange={onFileChange}
              />
            </div>

            {loading && (
              <div className="mt-6 text-center">
                <div className="inline-flex items-center space-x-2">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary"></div>
                  <span className="text-sm font-medium">Processing file...</span>
                </div>
              </div>
            )}

            {error && (
              <div className="mt-6 p-4 bg-destructive/10 border border-destructive/20 rounded-lg">
                <p className="text-sm text-destructive font-medium">{error}</p>
                <Button 
                  variant="outline" 
                  size="sm" 
                  className="mt-2"
                  onClick={resetState}
                >
                  Try Again
                </Button>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Results Section */}
        {result && !loading && (
          <div className="space-y-6">
            {/* Statistics */}
            <Card>
              <CardHeader>
                <CardTitle>Processing Summary</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  <div className="text-center p-4 bg-muted/50 rounded-lg">
                    <div className="text-2xl font-bold">{result.stats.totalFiles}</div>
                    <div className="text-sm text-muted-foreground">Total Files</div>
                  </div>
                  <div className="text-center p-4 bg-green-50 dark:bg-green-950/20 rounded-lg">
                    <div className="text-2xl font-bold text-green-600">{result.stats.icreitCount}</div>
                    <div className="text-sm text-muted-foreground">ICREIT</div>
                  </div>
                  <div className="text-center p-4 bg-blue-50 dark:bg-blue-950/20 rounded-lg">
                    <div className="text-2xl font-bold text-blue-600">{result.stats.icifCount}</div>
                    <div className="text-sm text-muted-foreground">ICIF</div>
                  </div>
                  <div className="text-center p-4 bg-orange-50 dark:bg-orange-950/20 rounded-lg">
                    <div className="text-2xl font-bold text-orange-600">{result.stats.unmatchedCount}</div>
                    <div className="text-sm text-muted-foreground">Unmatched</div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Download Section */}
            <Card>
              <CardHeader>
                <CardTitle>Download Sorted Files</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid md:grid-cols-2 gap-4">
                  <div className="flex items-center justify-between p-4 border rounded-lg">
                    <div>
                      <div className="font-medium">ICREIT Files</div>
                      <div className="text-sm text-muted-foreground">
                        {result.stats.icreitCount} files
                      </div>
                    </div>
                    <Button
                      onClick={() => handleDownload(result.ICREIT, "ICREIT_Documents.zip")}
                      disabled={result.stats.icreitCount === 0}
                      className="bg-green-600 hover:bg-green-700"
                    >
                      Download ZIP
                    </Button>
                  </div>
                  
                  <div className="flex items-center justify-between p-4 border rounded-lg">
                    <div>
                      <div className="font-medium">ICIF Files</div>
                      <div className="text-sm text-muted-foreground">
                        {result.stats.icifCount} files
                      </div>
                    </div>
                    <Button
                      onClick={() => handleDownload(result.ICIF, "ICIF_Documents.zip")}
                      disabled={result.stats.icifCount === 0}
                      className="bg-blue-600 hover:bg-blue-700"
                    >
                      Download ZIP
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Detailed File Lists */}
            <div className="grid md:grid-cols-2 gap-4">
              {/* ICREIT Files List */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-green-600">ICREIT Files</CardTitle>
                </CardHeader>
                <CardContent>
                  {result.reitFiles.length > 0 ? (
                    <div className="space-y-2">
                      <p className="text-sm text-muted-foreground mb-4">
                        Files categorized as ICREIT ({result.reitFiles.length} files):
                      </p>
                      <div className="max-h-60 overflow-y-auto space-y-1">
                        {result.reitFiles.map((filename, index) => (
                          <div 
                            key={index} 
                            className="p-2 bg-green-50 dark:bg-green-950/20 rounded text-sm font-mono break-all border-l-2 border-green-500"
                          >
                            {filename}
                          </div>
                        ))}
                      </div>
                    </div>
                  ) : (
                    <p className="text-sm text-muted-foreground">
                      No files found in ICREIT category.
                    </p>
                  )}
                </CardContent>
              </Card>

              {/* ICIF Files List */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-blue-600">ICIF Files</CardTitle>
                </CardHeader>
                <CardContent>
                  {result.icifFiles.length > 0 ? (
                    <div className="space-y-2">
                      <p className="text-sm text-muted-foreground mb-4">
                        Files categorized as ICIF ({result.icifFiles.length} files):
                      </p>
                      <div className="max-h-60 overflow-y-auto space-y-1">
                        {result.icifFiles.map((filename, index) => (
                          <div 
                            key={index} 
                            className="p-2 bg-blue-50 dark:bg-blue-950/20 rounded text-sm font-mono break-all border-l-2 border-blue-500"
                          >
                            {filename}
                          </div>
                        ))}
                      </div>
                    </div>
                  ) : (
                    <p className="text-sm text-muted-foreground">
                      No files found in ICIF category.
                    </p>
                  )}
                </CardContent>
              </Card>
            </div>

            {/* Unmatched Files */}
            <Card>
              <CardHeader>
                <CardTitle>Unmatched Files</CardTitle>
              </CardHeader>
              <CardContent>
                {result.unmatched.length > 0 ? (
                  <div className="space-y-2">
                    <p className="text-sm text-muted-foreground mb-4">
                      The following files did not match any IC numbers in the master list:
                    </p>
                    <div className="max-h-60 overflow-y-auto space-y-1">
                      {result.unmatched.map((filename, index) => (
                        <div 
                          key={index} 
                          className="p-2 bg-muted/50 rounded text-sm font-mono break-all"
                        >
                          {filename}
                        </div>
                      ))}
                    </div>
                  </div>
                ) : (
                  <p className="text-sm text-muted-foreground">
                    All files were successfully categorized! üéâ
                  </p>
                )}
              </CardContent>
            </Card>

            {/* Reset Button */}
            <div className="text-center">
              <Button variant="outline" onClick={resetState}>
                Process Another File
              </Button>
            </div>
          </div>
        )}

        {/* Info Section */}
        <Card>
          <CardHeader>
            <CardTitle>How it works</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4 text-sm text-muted-foreground">
            <div className="grid md:grid-cols-2 gap-6">
              <div>
                <h4 className="font-medium text-foreground mb-2">ICREIT Category</h4>
                <p className="mb-2">Files containing these IC numbers will be sorted into ICREIT:</p>
                <div className="text-xs font-mono bg-muted/50 p-2 rounded max-h-32 overflow-y-auto">
                  {ICREIT_NUMBERS.join(", ")}
                </div>
              </div>
              <div>
                <h4 className="font-medium text-foreground mb-2">ICIF Category</h4>
                <p className="mb-2">Files containing these IC numbers will be sorted into ICIF:</p>
                <div className="text-xs font-mono bg-muted/50 p-2 rounded max-h-32 overflow-y-auto">
                  {ICIF_NUMBERS.join(", ")}
                </div>
              </div>
            </div>
            <div className="pt-4 border-t">
              <p>
                <strong>Note:</strong> Files are categorized based on IC numbers found in their filenames. 
                Files that don't contain any recognized IC numbers will be listed as unmatched.
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
